import sendMsgCom from '@/components/send-msg/send-msg.vue';
export default {
	components: {
		sendMsgCom
	},
	data() {
		return {
			title: 'Hello',
			username: '',
			nickname: '',
			password: '',
			password2: '',
			msg_code: '',
			sex: 0,
			yzmstate: 0,
			yzmhtml: '获取验证码',
			activeColor: '#2CC17B',
			items: [{
					'name': '女',
					'value': 0
				},
				{
					'name': '男',
					'value': 1
				}
			],
			current: 0
		}
	},
	onLoad() {
		
	},
	methods: {
		clearInput(val) { //清空输入框
			if (val == 'username') {
				this.username = '';
			}
			if (val == 'nickname') {
				this.nickname = '';
			}
			if (val == 'password') {
				this.password = '';
			}
			if (val == 'password2') {
				this.password2 = '';
			}
		},
		radioChange(evt) { //单选框改变
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value == evt.target.value) {
					this.current = i;
					this.sex = evt.target.value;
					break;
				}
			}
			
		},
		register() {
			// 对比两次密码
			if (this.password != this.password2) {
				// 气泡提示
				uni.showToast({
					title: '两次密码不一致!',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			// 打开动画
			uni.showLoading({
				title: '注册中...',
				mask: true
			});
			uni.request({
				url: this.$serverUrl + '/api/login/register',
				method: 'POST',
				data: {
					phone_num: this.username,
					password:this.password,
					nickname:this.nickname,
					sex:this.sex,
					msg_code: this.msg_code
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							duration: 2000,
							success() {
								// 关闭动画
								uni.hideLoading();
								// 跳转到首页
								uni.reLaunch({
									url: 'login'
								});
							}
						});
					} else {
						// 关闭动画
						uni.hideLoading();
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		}
	}
}
